.App {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 1rem 4rem;
  text-align: center;
  background-image: url('./../Assets//Images/Background.jpg');
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100vw;
  display: grid;
  grid-template-rows: 4rem 1fr;
}
/*font-family: 'Long Cang', cursive;
font-family: 'Rock Salt', cursive;*/

.heading{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: 'Long Cang', cursive;
  font-size: 3rem;
  color: rgb(73, 63, 63);
  font-weight: 500;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-animation: animate 5s infinite linear;
          animation: animate 5s infinite linear;
  -webkit-text-decoration: underline solid rgb(66, 61, 61);
          text-decoration: underline solid rgb(66, 61, 61);
}

@-webkit-keyframes animate{
  0%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50%{
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes animate{
  0%{
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50%{
    -webkit-transform: translateY(-7px);
            transform: translateY(-7px);
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.Posts {
  grid-row: 2/3;
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(-webkit-min-content,20rem));
  grid-template-columns: repeat(auto-fit,minmax(min-content,20rem));
  grid-auto-rows: minmax(250px,-webkit-min-content);
  grid-auto-rows: minmax(250px,min-content);
  grid-gap: 2rem;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}


